import React, { useEffect } from 'react';
import * as THREE from "three";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { RoughnessMipmapper } from 'three/examples/jsm/utils/RoughnessMipmapper.js';
import { ARButton } from './jsm/webxr/ARButton';

let container;
let camera, scene, renderer;

let reticle, current_object, controls, shadow_plane, groupModel;

let hitTestSource = null;
let hitTestSourceRequested = false;
let enableUI = true;

let touchDown, touchX, touchY, deltaX, deltaY;

const pathModel = {
    local: 'ar-with-react/3d/', // local
    build: '3d/' // build
};

const App = () => {

    useEffect(() => {
        init();
        animate();

		document.getElementById('ARButton').addEventListener('click', function () {
			if(groupModel) groupModel.visible = false;
		})
    }, [])

    function openNav(e) {
        e.preventDefault();
        document.getElementById("mySidenav").style.width = "250px";
    }

    function closeNav(e) {
        e.preventDefault();
        document.getElementById("mySidenav").style.width = "0";
    }

    function arPlaceObject() {
        if (reticle.visible) {
            groupModel.position.setFromMatrixPosition(reticle.matrix);
            groupModel.visible = true;
        }
    }

    function init() {

        container = document.getElementById('container');

        scene = new THREE.Scene();

        camera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.001, 200);

        var directionalLight = new THREE.DirectionalLight(0xdddddd, 0.8);
        directionalLight.position.set(0, 5, -1);
        directionalLight.castShadow = true;
        scene.add(directionalLight);

        var ambientLight = new THREE.AmbientLight(0xffffff);
        scene.add(ambientLight);

        renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.shadowMap.enabled = true;
        renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        renderer.xr.enabled = true;
        container.appendChild(renderer.domElement);

        controls = new OrbitControls(camera, renderer.domElement);
        controls.addEventListener('change', render);

        controls.minDistance = 2;
        controls.maxDistance = 10;
        controls.target.set(0, 0, 0);

        var options = {
            requiredFeatures: ['hit-test'],
            optionalFeatures: ['dom-overlay']
        }

        options.domOverlay = { root: document.getElementById('ui-content') };
        document.body.appendChild(ARButton.createButton(renderer, options));

        reticle = new THREE.Mesh(
            new THREE.RingBufferGeometry(0.015, 0.02, 32).rotateX(- Math.PI / 2),
            new THREE.MeshBasicMaterial()
        );
        reticle.matrixAutoUpdate = false;
        reticle.visible = false;
        scene.add(reticle);

        window.addEventListener('resize', onWindowResize, false);

        renderer.domElement.addEventListener('touchstart', function (e) {
            e.preventDefault();
            touchDown = true;
            touchX = e.touches[0].pageX;
            touchY = e.touches[0].pageY;
        }, false)
        renderer.domElement.addEventListener('touchend', function (e) {
            e.preventDefault();
            touchDown = false;
        }, false)
        renderer.domElement.addEventListener('touchmove', function (e) {
            e.preventDefault();

            if (!touchDown) return;

            deltaX = e.touches[0].pageX - touchX;
            deltaY = e.touches[0].pageY - touchY;
            touchX = e.touches[0].pageX;
            touchY = e.touches[0].pageY;

            rotateObject();
        }, false)

        function rotateObject() {
            if (groupModel && reticle && hitTestSource) {
                groupModel.rotation.y += deltaX / 100;
            }
        }
    }

    function onWindowResize() {

        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        renderer.setSize(window.innerWidth, window.innerHeight);

    }

    function animate() {
        renderer.setAnimationLoop(render);
    }

    function loadModel(url) {
        if (groupModel) {
            scene.remove(groupModel);
        }

        document.getElementById('loader-model').style.display = 'block';
        new GLTFLoader()
            .setPath(pathModel.build) // path luôn được xác định từ folder chứa index.html
            .load(url, function (gltf) {
                current_object = gltf.scene;

                current_object.traverse(function (node) { // cast shadow trên từng thành phần của model
                    if ( node.isMesh ) { node.castShadow = true; }
                })

                let geometry = new THREE.PlaneBufferGeometry(200, 200);
                geometry.rotateX( - Math.PI / 2 );
                let material = new THREE.ShadowMaterial({ color: 0x000000 });
                material.opacity = 0.5;
                shadow_plane = new THREE.Mesh(geometry, material);
                shadow_plane.receiveShadow = true;

                groupModel = new THREE.Group();
                groupModel.add(current_object);
                groupModel.add(shadow_plane);

                scene.add(groupModel);

                var box = new THREE.Box3();
                box.setFromObject(groupModel);
                box.getCenter(controls.target);
                arPlaceObject();

                document.getElementById('loader-model').style.display = 'none';

                controls.update();
                render();
            });
    }

    function render(timestamp, frame) {

        if (frame) {

            const referenceSpace = renderer.xr.getReferenceSpace();
            const session = renderer.xr.getSession();

            if (hitTestSourceRequested === false) {

                session.requestReferenceSpace('viewer').then(function (referenceSpace) {

                    session.requestHitTestSource({ space: referenceSpace }).then(function (source) {

                        hitTestSource = source;

                    });

                });

                session.addEventListener('end', function () {

                    hitTestSourceRequested = false;
                    hitTestSource = null;

                    reticle.visible = false;
                    var box = new THREE.Box3();
                    box.setFromObject(groupModel);
                    box.getCenter(controls.target);

                    document.getElementById('place-button').style.display = 'none';
                    document.getElementById('group-zoom').style.display = 'none';

                });

                hitTestSourceRequested = true;

            }

            if (hitTestSource) {

                const hitTestResults = frame.getHitTestResults(hitTestSource);

                if (hitTestResults.length) {

                    const hit = hitTestResults[0];

                    document.getElementById('group-zoom').style.display = 'block';
                    if(enableUI) {
                        document.getElementById('place-button').style.display = 'block';
                        document.getElementById('zoom-in-button').style.display = 'block';
                        document.getElementById('zoom-out-button').style.display = 'block';
                        reticle.visible = true;
                    }
                    else{
                        document.getElementById('place-button').style.display = 'none';
                        document.getElementById('zoom-in-button').style.display = 'none';
                        document.getElementById('zoom-out-button').style.display = 'none';
                        reticle.visible = false;
                    }

                    
                    reticle.matrix.fromArray(hit.getPose(referenceSpace).transform.matrix);

                } else {

                    reticle.visible = false;

                }

            }

        }

        renderer.render(scene, camera);

    }

    return (
        <div style={{ width: '100%', height: '100%', overflow: 'hidden' }}>
            <div id="ui-content">
                <div id="mySidenav" className="sidenav">
                    <a href="#" className="closebtn" onClick={closeNav}>×</a>
                    <a className="ar-object" id="ar-obj-1" href="#" onClick={(e) => {
                        e.preventDefault();
                        loadModel('dancing_girl.gltf');
                    }}>item 1</a>
                    <a className="ar-object" id="ar-obj-2" href="#" onClick={(e) => {
                        e.preventDefault();
                        loadModel('matilda.gltf');
                    }}>item 2</a>
                    <a className="ar-object" id="ar-obj-3" href="#" onClick={(e) => {
                        e.preventDefault();
                        loadModel('samurai_mayumi_rescale.glb');
                    }}>item 3</a>
                </div>
                <span className="buttonMenu" style={{ fontSize: '30px', cursor: 'pointer' }} onClick={openNav}>☰</span>
                <button type="button" id="place-button" onClick={arPlaceObject}>PLACE</button>
                <div id="group-zoom">
                    <button id="hide-button" type="button"
                        onClick={() => {
                            let hideButton = document.getElementById('hide-button');
                            enableUI = !enableUI;
                            if(enableUI) hideButton.innerHTML = 'Hide UI';
                            else hideButton.innerHTML = 'Show UI';
                        }}
                    >Hide UI</button>
                    <button id="zoom-in-button" type="button" 
                        onClick={() => {
                            if(groupModel && reticle.visible) {
                                groupModel.scale.x += 0.1;
                                groupModel.scale.y += 0.1;
                                groupModel.scale.z += 0.1;
                            }
                        }}
                    >+</button>
                    <button id="zoom-out-button" type="button"
                        onClick={() => {
                            if (groupModel && reticle.visible && groupModel.scale.x > 0.2 && groupModel.scale.y > 0.2 && groupModel.scale.z > 0.2) {
                                groupModel.scale.x -= 0.1;
                                groupModel.scale.y -= 0.1;
                                groupModel.scale.z -= 0.1;
                            }
                        }}
                    >-</button>
                </div>
                <div id="container" style={{ position: 'fixed' }}>
                </div>
                <div id="loader-model"><span>Loading...</span></div>
            </div>

        </div>
    );
}

export default App;
